import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Aqsc from '@/components/pages/Aqsc'
import Wmsg from '@/components/pages/Wmsg'
import Sgcf from '@/components/pages/Sgcf'
import Addexamine from '@/components/pages/Addexamine'
import Addpunish from '@/components/pages/Addpunish'
import Addwmsg from '@/components/pages/Addwmsg'
import Ysjl from '@/components/pages/Ysjl'
import Detail from '@/components/pages/Detail'


Vue.use(Router)

export default new Router({
  routes: [
    {
			path: '/',
			redirect: '/home'
		},
		{
			path: '/home', //path:hash
			name: 'Home', //
			component: Home //
		},
		{
			path: '/home/aqsc', //path:hash
			name: 'Aqsc', //
			component: Aqsc //
		},
		{
			path: '/home/wmsg', //path:hash
			name: 'Wmsg', //
			component: Wmsg //
		},
				{
			path: '/home/sgcf', //path:hash
			name: 'Sgcf', //
			component: Sgcf //
		},
						{
			path: '/home/aqsc/addexamine', //path:hash
			name: 'Addexamine', //
			component: Addexamine //
		},
						{
			path: '/home/sgcf/addpunish', //path:hash
			name: 'Addpunish', //
			component: Addpunish //
		},
						{
			path: '/home/wmsg/addwmsg', //path:hash
			name: 'Addwmsg', //
			component: Addwmsg //
		},
								{
			path: '/home/aqsc/addexamine/ysjl', //path:hash
			name: 'Ysjl', //
			component: Ysjl //
		},
										{
			path: '/home/aqsc/addexamine/ysjl/detail', //path:hash
			name: 'Detail', //
			component: Detail //
		},
  ]
})



